<template>
  <div style="width: 100%">
    <CarouselPage />

    <div v-if="historyMovies.length > 0">
      <v-row class="category-header" align="center" no-gutters>
        <v-col cols="auto">
          <h2 class="category-title">
            <v-icon size="20" color="#ffcc00" class="mr-1">mdi-history</v-icon>
            {{ $t('LỊCH SỬ ĐÃ XEM') }}
          </h2>
        </v-col>
        <v-col cols="auto">
          <router-link :to="{ name: 'HistoryPage' }" class="view-all">
            {{ $t('Xem tất cả') }} >>
          </router-link>
        </v-col>
      </v-row>
      
      <div class="horizontal-scroll-container">
        <div
          v-for="(item, index) in historyMovies.slice(0, 12)"
          :key="item.slug || index"
          class="movie-item-wrapper"
        >
          <router-link
            :to="{ name: 'MovieDetail', params: { slug: item.slug }, query: { tap: item.episode?.slug } }"
          >
            <v-card class="mx-auto bg-dark text-white" max-width="344">
              <v-img
                :src="getOptimizedImage(item.poster_url)"
                :alt="`Poster phim ${item.name}`"
                class="movie-img"
                height="250"
                cover
              ></v-img>

              <v-card-subtitle class="episode-lang">
                Tập: {{ item.episode?.name || 'N/A' }}
              </v-card-subtitle>
              
              <v-card-title class="movie-title">{{ item.name }}</v-card-title>
              
              <v-card-text class="movie-info">
                <div class="text-grey text-truncate">
                  <v-icon size="14" class="mr-1" color="grey">mdi-tag</v-icon>
                  {{ item.origin_name }} ({{ item.year }})
                </div>
              </v-card-text>
            </v-card>
          </router-link>
        </div>
      </div>
    </div>
    
    <div v-for="(section, sectionIndex) in sections" :key="sectionIndex">
      <v-row class="category-header" align="center" no-gutters>
        <v-col cols="auto">
          <h2 class="category-title">
            <v-icon size="20" color="#ffcc00" class="mr-1">mdi-filmstrip</v-icon>
            {{ section.title }}
          </h2>
        </v-col>
        <v-col cols="auto">
          <router-link
            :to="
              section.name === 'QuocGia' || section.name === 'PhimNew'
                ? {
                    name: section.name,
                    params: { path: section.id.split('/')[1] },
                  }
                : { name: section.name }
            "
            class="view-all"
          >
            {{$t('Xem tất cả')}} >>
          </router-link>
        </v-col>
      </v-row>
      <div class="horizontal-scroll-container">
        <div
          v-for="(item, index) in isLoading
            ? Array(12).fill({})
            : section.listMovie.slice(0, 12)"
          :key="item.slug || index"
          class="movie-item-wrapper"
        >
          <v-skeleton-loader v-if="isLoading" type="image" height="250" />
          <router-link
            :to="{ name: 'MovieDetail', params: { slug: item.slug } }"
            v-else
          >
            <v-card class="mx-auto bg-dark text-white" max-width="344">
              <v-img
                :src="getOptimizedImage(item.poster_url)"
                :alt="`Poster phim ${item.name}`"
                class="movie-img"
                height="250"
                cover
              >
              </v-img>

              <v-card-subtitle class="episode-lang">
                {{
                  item.episode_current === "Tập 0"
                    ? `Full - ${item.lang}`
                    : `${item.episode_current} - ${item.lang}`
                }}
              </v-card-subtitle>

              <v-card-title class="movie-title">{{ item.name }}</v-card-title>

              <v-card-text class="movie-info">
                <div class="text-grey text-truncate">
                  <v-icon size="14" class="mr-1" color="grey">mdi-tag</v-icon>
                  {{ item.origin_name }} ({{ item.year }})
                </div>
              </v-card-text>
            </v-card>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { ListMovieByCateHome, urlImage } from "@/model/api";
import CarouselPage from "./Carousel.vue";

export default {
  name: "HomePage",
  data() {
    return {
      urlImage: urlImage,
      isLoading: true,
      historyMovies: [],
      sections: [
        {
          title: this.$t('PHIM THỊNH HÀNH'),
          id: "danh-sach/thinh-hanh",
          name: "PhimNew",
          listMovie: [],
          content: ''
        },
        {
          title: this.$t("PHIM ĐỀ CỬ"),
          id: "danh-sach/phim-moi-cap-nhat?page=2",
          name: "PhimNew",
          listMovie: [],
          content: ''

        },
        {
          title: this.$t("PHIM BỘ"),
          id: "danh-sach/phim-bo",
          name: "PhimBo",
          listMovie: [],
          content: ''

        },
        {
          title: this.$t("PHIM LẺ"),
          id: "danh-sach/phim-le",
          name: "PhimLe",
          listMovie: [],
          content: ''

        },
        {
          title: this.$t("PHIM HÀN QUỐC"),
          id: "quoc-gia/han-quoc",
          name: "QuocGia",
          listMovie: [],
          content: ''

        },
        {
          title: this.$t("PHIM TRUNG QUỐC"),
          id: "quoc-gia/trung-quoc",
          name: "QuocGia",
          listMovie: [],
          content: ''

        },
      ],
    };
  },
  components: {
    CarouselPage,
  },
  mounted() {
    this.loadHistory();
    this.sections.forEach((item) => {
      this.ListMovie(item.id, item);
    });
  },
  methods: {
    loadHistory() {
      const history = JSON.parse(localStorage.getItem('watchHistory') || '[]');
      this.historyMovies = history.slice(0, 12).sort((a, b) => b.timestamp - a.timestamp);
    },
    ListMovie(sectionId, section) {
      ListMovieByCateHome(
        sectionId,
        (result) => {
          if (result.status === "success") {
            section.listMovie = result.data.items;
            
             if (result.data.seoOnPage) {
                this.updateMetaTags(result.data.seoOnPage)
              }
            this.isLoading = false;
          }
        },
        (err) => {
          console.error(err);
        }
      );
    },
    getOptimizedImage(imagePath) {
      if (imagePath && imagePath.startsWith('https://')) {
          return imagePath;
      }
      return imagePath ? `${this.urlImage}${encodeURIComponent(imagePath)}&w=384&q=100` : '/placeholder.jpg';
    },
    updateMetaTags(seo) {
    document.title = seo.titleHead || 'Phim hay'

    const removeOldMeta = (key, attr = 'name') => {
      const old = document.querySelectorAll(`meta[${attr}="${key}"]`)
      old.forEach(tag => tag.remove())
    }

    const setMeta = (key, content, attr = 'name') => {
      if (!content) return
      const meta = document.createElement('meta')
      meta.setAttribute(attr, key)
      meta.setAttribute('content', content)
      document.head.appendChild(meta)
    }

    removeOldMeta('description')
    removeOldMeta('og:title', 'property')
    removeOldMeta('og:description', 'property')
    removeOldMeta('og:type', 'property')
    removeOldMeta('og:image', 'property')

    setMeta('description', seo.descriptionHead)
    setMeta('og:title', seo.titleHead, 'property')
    setMeta('og:description', seo.descriptionHead, 'property')
    setMeta('og:type', seo.og_type || 'website', 'property')

    if (Array.isArray(seo.og_image)) {
      seo.og_image.forEach(img => {
        setMeta('og:image', img, 'property')
      })
    }
  },
  },
};
</script>

